import React, { useMemo, useState, useEffect } from 'react';
import {
  MRT_EditActionButtons,
  MaterialReactTable,
  useMaterialReactTable,
} from 'material-react-table';
import {
  Box,
  Button,
  DialogActions,
  DialogContent,
  DialogTitle,
  IconButton,
  Tooltip,
  TextField,
} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import axiosInstance from '../api/api.js'; // Import axiosInstance

const PasswordManager = () => {
  const [validationErrors, setValidationErrors] = useState({});
  const [passwords, setPasswords] = useState([]);
  const [loading, setLoading] = useState(false);
  const [newPassword, setNewPassword] = useState({
    website: '',
    username: '',
    password: '',
  });
  const [editingPasswordId, setEditingPasswordId] = useState(null);

  useEffect(() => {
    fetchPasswords();
  }, []);

  const columns = useMemo(
    () => [
      {
        accessorKey: 'id',
        header: 'Id',
        enableEditing: false,
        size: 80,
      },
      {
        accessorKey: 'website',
        header: 'Website',
        muiEditTextFieldProps: {
          type: 'text',
          required: true,
          error: !!validationErrors?.website,
          helperText: validationErrors?.website,
          onFocus: () =>
            setValidationErrors({
              ...validationErrors,
              website: undefined,
            }),
        },
      },
      {
        accessorKey: 'username',
        header: 'Username',
        muiEditTextFieldProps: {
          type: 'text',
          required: true,
          error: !!validationErrors?.username,
          helperText: validationErrors?.username,
          onFocus: () =>
            setValidationErrors({
              ...validationErrors,
              username: undefined,
            }),
        },
      },
      {
        accessorKey: 'password',
        header: 'Password',
        muiEditTextFieldProps: {
          type: 'text',
          required: true,
          error: !!validationErrors?.password,
          helperText: validationErrors?.password,
          onFocus: () =>
            setValidationErrors({
              ...validationErrors,
              password: undefined,
            }),
        },
      },
    ],
    [validationErrors]
  );

  const fetchPasswords = async () => {
    setLoading(true);
    try {
      const response = await axiosInstance.get('/getPasswords');
      setPasswords(response.data);
    } catch (error) {
      console.error('Error fetching passwords:', error);
      // Handle error as needed
    } finally {
      setLoading(false);
    }
  };

  const handleAddPassword = async (e) => {
    e.preventDefault();
    try {
      const response = await axiosInstance.post('/addPassword', newPassword);
      setPasswords([...passwords, response.data]); // Update the UI optimistically
      setNewPassword({ website: '', username: '', password: '' });
    } catch (error) {
      console.error('Error adding password:', error);
      // Handle error as needed
    }
  };

  const handleEditPassword = async (id, updatedPassword) => {
    try {
      await axiosInstance.put(`/updatePassword/${id}`, updatedPassword);
      setEditingPasswordId(null);
      fetchPasswords();
    } catch (error) {
      console.error('Error updating password:', error);
      // Handle error as needed
    }
  };

  const handleDeletePassword = async (id) => {
    try {
      await axiosInstance.delete(`/deletePassword/${id}`);
      setPasswords(passwords.filter((p) => p.id !== id)); // Update the UI optimistically
    } catch (error) {
      console.error('Error deleting password:', error);
      // Handle error as needed
    }
  };

  const generateRandomPassword = () => {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*()_+{}:"<>?[];,./`~';
    const passwordLength = 12;
    let password = '';
    for (let i = 0; i < passwordLength; i++) {
      const randomIndex = Math.floor(Math.random() * chars.length);
      password += chars[randomIndex];
    }
    setNewPassword({ ...newPassword, password });
  };

  const table = useMaterialReactTable({
    columns,
    data: passwords,
    createDisplayMode: 'modal',
    editDisplayMode: 'modal',
    enableEditing: true,
    getRowId: (row) => row.id,
    onCreatingRowCancel: () => setValidationErrors({}),
    onEditingRowCancel: () => setValidationErrors({}),
    renderCreateRowDialogContent: ({ table, row, internalEditComponents }) => (
      <>
        <DialogTitle variant="h3">Create New Password</DialogTitle>
        <DialogContent sx={{ display: 'flex', flexDirection: 'column', gap: '1rem' }}>
          {internalEditComponents}
        </DialogContent>
        <DialogActions>
          <MRT_EditActionButtons variant="text" table={table} row={row} />
        </DialogActions>
      </>
    ),
    renderEditRowDialogContent: ({ table, row, internalEditComponents }) => (
      <>
        <DialogTitle variant="h3">Edit Password</DialogTitle>
        <DialogContent sx={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
          {internalEditComponents}
        </DialogContent>
        <DialogActions>
          <MRT_EditActionButtons variant="text" table={table} row={row} />
        </DialogActions>
      </>
    ),
    renderRowActions: ({ row, table }) => (
      <Box sx={{ display: 'flex', gap: '1rem' }}>
        <Tooltip title="Edit">
          <IconButton onClick={() => setEditingPasswordId(row.original.id)}>
            <EditIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Delete">
          <IconButton color="error" onClick={() => handleDeletePassword(row.original.id)}>
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      </Box>
    ),
    state: {
      isLoading: loading,
      isSaving: false,
      showAlertBanner: false,
      showProgressBars: false,
    },
  });

  return (
    <div className="container">
      <h1>Password Manager</h1>
      <Button variant="contained" onClick={generateRandomPassword}>
        Generate Password
      </Button>
      <form onSubmit={handleAddPassword}>
        <TextField
          type="text"
          label="Website"
          variant="outlined"
          fullWidth
          value={newPassword.website}
          onChange={(e) => setNewPassword({ ...newPassword, website: e.target.value })}
        />
        <TextField
          type="text"
          label="Username"
          variant="outlined"
          fullWidth
          value={newPassword.username}
          onChange={(e) => setNewPassword({ ...newPassword, username: e.target.value })}
        />
        <TextField
          type="text"
          label="Password"
          variant="outlined"
          fullWidth
          value={newPassword.password}
          onChange={(e) => setNewPassword({ ...newPassword, password: e.target.value })}
        />
        <Button type="submit" variant="contained" color="primary">
          Add Password
        </Button>
      </form>
      <MaterialReactTable table={table} />
    </div>
  );
};

export default PasswordManager;
